// reactstrap components
import {
    Card,
    CardHeader,
    Table,
    Row
} from "reactstrap";
import { getContactDetail } from "core/services/api/devicedetails/contact";
import { useEffect, useState } from "react";
// import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import moment from "moment";
import ReactHTMLTableToExcel from "react-html-table-to-excel";



const Contact = () => {

    const [contactDetailsLists, setContactDetailsLists] = useState([]);
    const device_id = "31";
    const limit = 20;
    const offset = 20;

    console.log("-----Full Data----->", contactDetailsLists);
    const fetchContactDetailsList = async () => {
        const values = { device_id, limit, offset }


        const result = await getContactDetail(values);
        if (result && result.data && result.data.result) {
            setContactDetailsLists(result.data.result)


        }

    }



    useEffect(() => {
        fetchContactDetailsList();
    }, [])






    return (
        <>

            {/* Table */}
            <Row>
                <div className="col">
                    <Card className="shadow">
                        <CardHeader className="border-0">
                            <div style={{ display: "flex" }}>
                                <div className="col-4">
                                    <h3 className="mb-0">Contact List</h3></div>
                                <div className="col-4">
                                </div>
                                <div className="col-4">
                                    <ReactHTMLTableToExcel
                                        id="test-table-xls-button"
                                        className="download-button"
                                        table="table-to-xls-contact"
                                        filename="tablexls"
                                        sheet="tablexls"
                                        buttonText="Export" />
                                </div>
                            </div>
                        </CardHeader>
                        <Table className="align-items-center table-flush" id="table-to-xls-contact" responsive style={{ padding: "5%" }}>
                            <thead className="thead-light">
                                <tr>
                                    <th scope="col">Person Name </th>
                                    <th scope="col">Email Address</th>
                                    <th scope="col">Phone Number</th>
                                    <th scope="col">Date/Time</th>
                                    <th scope="col">Relationship</th>


                                </tr>
                            </thead>


                            <tbody>
                                {
                                    contactDetailsLists.length > 0 && contactDetailsLists.map((contactDetailsList, index) => (
                                        <tr
                                            key={index}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >

                                            <td>{contactDetailsList.person_name}</td>
                                            <td>
                                                {contactDetailsList.email_address ? contactDetailsList.email_address : 'N/A'}
                                            </td>
                                            <td>
                                                {contactDetailsList.phone_number}
                                            </td>
                                            <td className="table-col">
                                                {moment(contactDetailsList.call_date_time).format("DD/MM/YYYY")}<br />
                                                {moment(contactDetailsList.call_date_time).format("h:mm a")}

                                            </td>
                                            <td>{contactDetailsList.relationship ? contactDetailsList.relationship : "N/A"}</td>


                                        </tr>))}


                            </tbody>
                        </Table>
                    </Card>
                </div>
            </Row>
            {/* Dark table */}


        </>
    );
};

export default Contact;
