import React from 'react';
import Header from 'components/Headers/Header';
import { Container, Card, CardHeader } from 'reactstrap';
import { Row, Col, Nav } from 'reactstrap';
import { NavItem, NavLink, TabContent, TabPane } from 'reactstrap';
import classnames from 'classnames';
import { useState } from 'react';
import Calls from "views/private/devicedetails/calls"
import Messages from 'views/private/devicedetails/messages';
import Contact from 'views/private/devicedetails/contact';
import Notification from 'views/private/devicedetails/notification';
import Audio from 'views/private/devicedetails/audio';
import "../../../assets/css/devicedetails.css";
import { AiFillPhone, AiFillContacts, AiFillNotification, AiFillAudio, AiFillMessage, AiFillBackward } from 'react-icons/ai';
import { useHistory } from 'react-router';


const DeviceDetails = () => {
    const [currentActiveTab, setCurrentActiveTab] = useState('1');

    // Toggle active state for Tab
    const toggle = tab => {
        if (currentActiveTab !== tab) setCurrentActiveTab(tab);
    }

    const history = useHistory("");

    const onHandleBack = () => {

        history.push("/admin/devices");
    }



    return (


        <>

            <Header show={true} />
            <Container className="mt--7" fluid>
                <div class="container">
                    <Card className="shadow">

                        <CardHeader className="border-0">
                            <h3 className="mb-0">Device Information</h3>
                        </CardHeader>


                        <Row >
                            <Col xs="2" sm="2" md="2" >

                                <Nav tabs vertical className="nav-tabs">

                                    <NavItem  >
                                        <NavLink
                                            className={classnames({

                                                active:
                                                    currentActiveTab === '1'
                                            })}
                                            onClick={() => { toggle('1'); }}
                                        >
                                            <AiFillPhone size={20} />   Calls
                                        </NavLink>
                                    </NavItem>
                                    <NavItem  >
                                        <NavLink

                                            className={classnames({
                                                active:
                                                    currentActiveTab === '2'
                                            })}
                                            onClick={() => { toggle('2'); }}
                                        >
                                            <AiFillContacts /> Contact
                                        </NavLink>
                                    </NavItem>
                                    <NavItem  >
                                        <NavLink
                                            className={classnames({
                                                active:
                                                    currentActiveTab === '3'
                                            })}
                                            onClick={() => { toggle('3'); }}
                                        >
                                            <AiFillMessage />   Messages
                                        </NavLink>
                                    </NavItem>
                                    <NavItem  >
                                        <NavLink
                                            className={classnames({
                                                active:
                                                    currentActiveTab === '4'
                                            })}
                                            onClick={() => { toggle('4'); }}
                                        >
                                            <AiFillNotification />  Notification
                                        </NavLink>
                                    </NavItem>
                                    <NavItem >
                                        <NavLink
                                            className={classnames({
                                                active:
                                                    currentActiveTab === '5'
                                            })}
                                            onClick={() => { toggle('5'); }}
                                        >
                                            <AiFillAudio /> Audio
                                        </NavLink>
                                    </NavItem>
                                    <NavItem >
                                        <NavLink onClick={onHandleBack}  >
                                            <AiFillBackward /> Back
                                        </NavLink>
                                    </NavItem>


                                </Nav>



                            </Col>
                            <Col xs="10" sm="10" md="10">
                                <TabContent activeTab={currentActiveTab}>
                                    <TabPane tabId="1">
                                        <Row className="row-height">
                                            <Col >
                                                <Calls />
                                            </Col>
                                        </Row>
                                    </TabPane>
                                    <TabPane tabId="2">
                                        <Row className="row-height">
                                            <Col >
                                                <Contact />

                                            </Col>
                                        </Row>
                                    </TabPane>
                                    <TabPane tabId="3">
                                        <Row className="row-height">
                                            <Col >
                                                <Messages />
                                            </Col>
                                        </Row>
                                    </TabPane>
                                    <TabPane tabId="4">
                                        <Row className="row-height">
                                            <Col >
                                                <Notification />
                                            </Col>
                                        </Row>
                                    </TabPane>
                                    <TabPane tabId="5">
                                        <Row className="row-height">
                                            <Col>
                                                <Audio />
                                            </Col>
                                        </Row>
                                    </TabPane>


                                </TabContent>
                            </Col>
                        </Row>



                    </Card>




                </div>

            </Container>

        </>

    )
};
export default DeviceDetails;