// reactstrap components
import {
    Card,
    CardHeader,
    Table,
    Row
} from "reactstrap";
import { getSmsDetail } from "core/services/api/devicedetails/sms";
import { useEffect, useState } from "react";
// import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import moment from "moment";
import ReactHTMLTableToExcel from "react-html-table-to-excel"




const Messages = () => {

    const [smsDetailsLists, setSmsDetailsLists] = useState([]);
    const device_id = "31";
    const limit = 20;
    const offset = 0;

    console.log("-----Full Data----->", smsDetailsLists);
    const fetchSmsDetailsList = async () => {
        const values = { device_id, limit, offset }
        const result = await getSmsDetail(values);
        if (result && result.data && result.data.result) {
            setSmsDetailsLists(result.data.result)
        }

    }
    useEffect(() => {
        fetchSmsDetailsList();
    }, [])

    return (
        <>

            {/* Table */}
            <Row>
                <div className="col">
                    <Card className="shadow">
                        <CardHeader className="border-0">
                            <div style={{ display: "flex" }}>
                                <div className="col-4">
                                    <h3 className="mb-0">Messages List</h3></div>
                                <div className="col-4">
                                </div>
                                <div className="col-4">
                                    <ReactHTMLTableToExcel
                                        id="test-table-xls-button"
                                        className="download-button"
                                        table="table-to-xls-messages"
                                        filename="tablexls"
                                        sheet="tablexls"
                                        buttonText="Export" />
                                </div>
                            </div>
                        </CardHeader>
                        <Table className="align-items-center table-flush" id="table-to-xls-messages" responsive style={{ padding: "5%" }}>
                            <thead className="thead-light">
                                <tr>
                                    <th scope="col">Sms Name </th>
                                    <th scope="col">Message</th>
                                    <th scope="col">Phone Number</th>
                                    <th scope="col">Date/Time</th>
                                </tr>
                            </thead>


                            <tbody>
                                {
                                    smsDetailsLists.length > 0 && smsDetailsLists.map((smsDetailsList, index) => (
                                        <tr
                                            key={index}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >
                                            <td>{smsDetailsList.sms_name ? smsDetailsList.sms_name : "Null"}</td>
                                            <td
                                            //  style={{ whiteSpace: "break-spaces" }}
                                            >
                                                {smsDetailsList.sms_message}
                                            </td>
                                            <td>
                                                {smsDetailsList.phone_number}
                                            </td>
                                            <td style={{ whiteSpace: "break-spaces" }}>
                                                {moment(smsDetailsList.sms_time).format("DD/MM/YYYY")}<br />
                                                {moment(smsDetailsList.sms_time).format("h:mm a")}

                                            </td>
                                        </tr>))}


                            </tbody>
                        </Table>
                    </Card>
                </div>
            </Row>
            {/* Dark table */}


        </>
    );
};

export default Messages;
