// reactstrap components
import {
    Card,
    CardHeader,
    Table,
    Row
} from "reactstrap";
import { getNotificationDetail } from "core/services/api/devicedetails/notification";
import { useEffect, useState } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import "assets/css/notification.css";
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css



const Notification = () => {

    const [notificationDetailsLists, setNotificationDetailsLists] = useState([]);
    const device_id = "74";
    const limit = 20;
    const offset = 0;

    console.log("-----Full Data----->", notificationDetailsLists);
    const fetchNotificationDetailsList = async () => {
        const values = { device_id, limit, offset }
        const result = await getNotificationDetail(values);
        if (result && result.data && result.data.result) {
            setNotificationDetailsLists(result.data.result)
        }

    }
    useEffect(() => {
        fetchNotificationDetailsList();
    }, [])

    return (
        <>

            {/* Table */}
            <Row>
                <div className="col">
                    <Card className="shadow">
                        <CardHeader className="border-0">
                            <div style={{ display: "flex" }}>
                                <div className="col-4">
                                    <h3 className="mb-0">Notification List</h3></div>
                                <div className="col-4">
                                </div>
                                <div className="col-4">
                                    <ReactHTMLTableToExcel
                                        id="test-table-xls-button"
                                        className="download-button"
                                        table="table-to-xls-notification"
                                        filename="tablexls"
                                        sheet="tablexls"
                                        buttonText="Export" />
                                </div>
                            </div>
                        </CardHeader>
                        <Table className="align-items-center table-flush" id="table-to-xls-notification" responsive style={{ padding: "5%" }}>
                            <thead className="thead-light">
                                <tr>
                                    <th scope="col">Package Name </th>
                                    <th scope="col">Title</th>
                                    <th scope="col">Text</th>
                                    <th scope="col">Status</th>


                                </tr>
                            </thead>


                            <tbody>
                                {
                                    notificationDetailsLists.length > 0 && notificationDetailsLists.map((notificationDetailsList, index) => (
                                        <tr
                                            key={index}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >
                                            <td >{notificationDetailsList.package_name}</td>
                                            <td>{notificationDetailsList.notification_title ? notificationDetailsList.notification_title : 'Null'}</td>
                                            <td>{notificationDetailsList.notification_text}</td>
                                            <td>{notificationDetailsList.notification_status ? notificationDetailsList.notification_status : "Null"}</td>
                                        </tr>))}


                            </tbody>
                        </Table>
                    </Card>
                </div>
            </Row>
            {/* Dark table */}


        </>
    );
};

export default Notification;
