// reactstrap components
import {
    Card,
    CardHeader,
    Table,
    Row
} from "reactstrap";
import { getCallDetail } from "core/services/api/devicedetails/calls";
import { useEffect, useState } from "react";
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import moment from "moment";
import "assets/css/devicedetails.css"
import ReactHTMLTableToExcel from "react-html-table-to-excel";



const Calls = () => {

    const [callDetailsLists, setCallDetailsLists] = useState([]);
    const device_id = "31";
    const limit = 20;
    const offset = 20;

    const fetchCallDetailsList = async () => {
        const values = { device_id, limit, offset }
        const result = await getCallDetail(values);
        if (result && result.data && result.data.result) {
            setCallDetailsLists(result.data.result);
        }

    }



    useEffect(() => {
        fetchCallDetailsList();
    }, [])







    return (
        <>

            {/* Table */}
            <Row>
                <div className="col">
                    <Card className="shadow">
                        <CardHeader className="border-0">
                            <div style={{ display: "flex" }}>
                                <div className="col-4">
                                    <h3 className="mb-0">Call List</h3></div>
                                <div className="col-4">
                                </div>
                                <div className="col-4">
                                    <ReactHTMLTableToExcel
                                        id="test-table-xls-button"
                                        className="download-button"
                                        table="table-to-xls-calls"
                                        filename="tablexls"
                                        sheet="tablexls"
                                        buttonText="Export" />
                                </div>
                            </div>

                        </CardHeader>
                        <Table className="align-items-center table-flush" id="table-to-xls-calls" responsive>
                            <thead className="thead-light">
                                <tr>

                                    <th scope="col">Phone Number </th>
                                    <th scope="col">Call Type</th>
                                    <th scope="col">Date/Time</th>
                                    <th scope="col">Duration</th>


                                </tr>
                            </thead>


                            <tbody>
                                {
                                    callDetailsLists.length > 0 && callDetailsLists.map((callDetailsList, index) => (
                                        <tr
                                            key={index}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >
                                            <td>
                                                {callDetailsList.b_party_phone_no}
                                            </td>
                                            <td>
                                                {callDetailsList.call_type}
                                            </td>
                                            <td >
                                                {moment(callDetailsList.call_date_time).format("DD/MM/YYYY")}<br />
                                                {moment(callDetailsList.call_date_time).format("h:mm a")}
                                            </td>
                                            <td>{callDetailsList.duration}</td>
                                        </tr>))}


                            </tbody>
                        </Table>
                    </Card>
                </div>
            </Row>
            {/* Dark table */}


        </>
    );
};

export default Calls;
